.header {
  padding: 30px 0 35px;
  background: $black-peral-to-right;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;

  @include vp-1023 {
    padding: 20px 0 25px;
  }

  @include vp-767 {
    padding: 15px 0 20px;
  }

  &__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  &__contacts {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 465px;
    margin-bottom: 8px;

    @include vp-1023 {
      width: 100px;
      height: 40px;
      margin-bottom: 15px;
      margin-right: 13px;
    }

    @include vp-767 {
      margin-bottom: 0;
      margin-right: 10px;
      margin-top: -5px;
    }
  }

  &__link {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: "PT Mono", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;

    color: $color-alice-blue;
    margin-right: 5px;

    @include vp-1023 {
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      margin-right: 0;
    }
  }

  &__link-text {
    @include vp-1023 {
      display: none;
    }
  }

  &__svg {
    width: 20px;
    height: 20px;
    margin-right: 15px;

    @include vp-767 {
      margin-right: 0;
    }
  }

  &__callback {
    margin-right: 60px;
    margin-bottom: 5px;

    @include vp-1023 {
      display: none;
    }
  }
}
